.page {
    width: 90%;
    margin: auto;
color: #eee;
}

.title {
    color: #eee;
    margin-top: 2rem;
    font-size: 30px;
    font-weight: bold;
    /* color: #ffaa0d */
}

.cards {
    display: flex;
    gap: 10px;
    flex-direction: column;
}

.btn {
    padding: 1rem;
    margin: 10px;
    border: #1b98e0 1px solid;
    color: #1b98e0;
    transition: all .3s;
}

.btn:hover{
    color: #eee;
    cursor: pointer;
    background-color: #1b98e0;
}