.page{
    color: #ccc;
}

.title {
    font-size: 1.5rem;
    color: #eee;
    font-weight:bold;
    margin: 20px;
}

.form{
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 500px;
    margin: auto;
    gap: 5px;
}

.form input{
    background: none;
    outline: none;
    border: #1b98e0 1px  solid;
    padding: 10px 5px;
    color: #ccc;
}

.btn {
    padding: 10px ;
    color: #1b98e0;
    background: none;
    cursor: pointer;
    border: #1b98e0 1px solid;
    transition: all .2s;
}

.btn:hover{
    background: #1b98e0;
    color: #eee;
}