.nav {
    width: 140px;
    height: calc(100vh - 50px);
    background-color: #1b98e0;
    display: flex;
    flex-direction: column;
}

.opt {
    padding: 1rem;
    color: #eee;
    text-decoration: none;
    transition: all .3s;
}

.opt:hover{
    background-color: #00000055;

}

#on{
    background-color: #00000055;

}