.header{
    background-color: #1b98e0;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
}

.header img {
    width: 35px;
    height: 35px;
}

.title {
    color: #eee;
    font-weight: bolder;
    font-family: Poppins;
    font-size: 1.4rem;
    transition: all .4s;
    cursor: default;
}

.title:hover{
    transform: scale(1.1);
}

.header svg{
    transition: all .2s;
    cursor: pointer;
}

.header svg:hover{
    transform: scale(1.2);
}
