.page{
    color: #ccc;
}

.title {
    font-size: 1.5rem;
    color: #eee;
    font-weight:bold;
    margin: 20px;
}

.mailsContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.btn {
    background-color: #1b98e0;
    padding: 10px;
    width: auto;
    color: #eee;
    outline: none;
    border: none;
    transition: all .2s;
    outline: none;
    text-decoration: none;
    display: inline-block
}

.btn:hover {
    transform: scale(1.1);
    
}