.page {
    width: 100%;
    /* height: 100%; */
    display: flex;
    justify-content: center;
}

.form {
    max-width: 1000px;
    width: 90%;
}

.title {
    color: #eee;
    margin: 2rem;
    font-size: 30px;
    font-weight: bold;
}

.label {
    display: block;
    text-align: start;
    color: #eee;
    /* font-size: 1.3rem; */
    margin-bottom: 5px;
    font-size: 25px;
}

.msgInput {
    width: 100%;
    background: none;
    resize: none;
    border: 1px solid #eee;
    height:auto;
    overflow: visible;
    padding: 5px;
    font-size: 20px;
    border-radius: 5px;
    color: rgba(238, 238, 238, 0.76);
    box-sizing: border-box;

}

.labelNum {
    color: #fff;
    width: 100%;
    display: flex;
    height: 60px;
    background-color: #1b98e0;
    padding: 1rem;
    box-sizing: border-box;
    /* border-radius: 10px; */
    margin: 1rem 0;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: all .3s;
}

.inputNum:hover .labelNum{
    font-size: 1.2rem;
    z-index: 3;
}

.labelNum.hover {
    background-color: #1b98e098;
    border-width: 1px ;
    border-color: #eee;
    border-style: dashed;


}

.inputNum  {
    width: 100%;
    display: flex;
    height: 60px;
    max-width: 1000px;
    background-color: aqua;
    position: relative;
    top: -79px;
    opacity: 0;
    z-index: 1;
    cursor: pointer;
}

.btn {
    position: relative;
    top: -70px;
    margin-top: 10px;
    padding: 1rem 3rem;
    border-radius: 10px;
    color: #1b98e0;
    background: none;
    border: 1px solid #1b98e0;
    cursor: pointer;
    transition: all .3s;
}

.btn:hover {
    color: #eee;
    background-color: #1b98e0;
    transform: scale(1.1);
}

.pais {
    margin:  10px 0;
    display: flex;
    align-items: center;
    gap: 10px;
}

.pais input{
    width: 90px;
}

.pais div {
    color: #ccc;
    font-weight: bold;
    font-size: 20px;
}
.sms {
    color: #ccc;
    position: relative;
    top: -60px;
    
}

.btn:hover {
    color: #eee;
    background-color: #1b98e0;
    transform: scale(1.1);
}

.btnTest{
    position: relative;
    top: -70px;
    margin-top: 10px;
    padding: .5rem 0rem;
    border-radius: 10px;
    color: #eee;
    background: #ff0000;
    border: 1px solid #ff0000;
    cursor: pointer;
    transition: all .3s;
    width: 100px;
    margin: 10px auto;
}

.btnTest:hover {
    transform: scale(1.1);
}