.card {
    width: 100%;
    min-height: 80px;
    background-color: #1b98e0cc;
    display: flex;
    padding: 10px;
    justify-content: space-between;
    color: #000;


}

.message {
    color: #ccc;
    text-align: start;
    font-size: 12px;

    max-width: 60%;
}

.message .title {
    color: #eee;
    text-align: start;
    font-size: 15px;
}

.icons {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.icon {
    display: flex;
    align-items: center;
    gap: 2px;
}

.icon div:first-child{
    display: flex;
    align-items: center;
}

#end {
    opacity: 0.7;
    background-color: #00bb38cc;
}