.cad {
    width: 90%;
    max-width: 500px;
    height: 50px;
    background-color: #1b98e0;
    display: flex;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
    justify-content: space-between;
    color: #eee;
}

.btn {
    cursor: pointer;
    display: flex;
    transition: all .3s;
}

.btn:hover {
    transform: scale(1.1);
}